/* csstile demos (formerly vectortiles demos) */

ul.lines,
ul.dots {

  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0;
  list-style: none;
  padding: 0;
  margin: 0;
  grid-gap: 0;

  li {
    width: 100%;
    height: 250px;
    background-repeat: repeat;

    margin: 0;
    padding: 0;
    position: relative;
    box-sizing: border-box;
    transition: all 0.3s ease;

    &:before {
      background-color: white;
      color: #333;
      padding: 4px 15px;
      margin: 0 0 0 0;
      position: absolute;
      font-size: 12px;
      font-family: monospace;
      box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
    }

  }
}

ul.dots {

  &.single-dots li {


    &:nth-of-type(1) {
      $dot: (
      15%,
